.right-sidebar{
    float: right;
    width:300px ;
    margin:40px 0px 15px 24px ;
    font-size: 15px;
}
.widget{
    margin-top:10px ;
    box-shadow:3px 3px 10px rgb(0 0 0 / 5%),
    -3px -3px 10px rgb(0 0 0 / 5%) ;
}
.widget h4{
    background-color: #fbf3d5;
    margin: 0%;
    padding: 15px;
    border: solid 1px #f1e5bc;
}
.right-sidebar-div-1{
    background-color: #fbf7e2;
    padding: 15px;
    border: solid 1px #f1e5bc;

}
.right-sidebar-div-1 .right-sidebar-div-2{
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
}
.right-sidebar-div-1 .right-sidebar-div-2 p{
    padding-left: 10px;
    margin-top: 0%;
    font-size: 14px;
}
.widget-tags{
    margin-top: 40px;
    box-shadow:3px 3px 10px rgb(0 0 0 / 5%),
    -3px -3px 10px rgb(0 0 0 / 5%) ;
}
.widget-tags h4{
    margin: 0%;
    padding: 15px;
    background-color: #f8f8f9;
    border: solid 1px #e3e6e8;
}
.widget-tags-div{
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-evenly;
    border: solid 1px #e3e6e8;
    padding: 10px;
}
.widget-tags-div p{
    padding: 5px;
    background-color: #e1ecf4;
    color: #39739d;
    border-radius: 2px;
    font-size: 14px;
}
@media(max-width:490px){
    .right-sidebar{
        width: auto;
        padding: 0%;
        margin: 0%;
    }
}